				<router-view></router-view>


		</div>
	

        <div id="footer_footer">
				<van-tabbar v-model="active"  >
			  <van-tabbar-item icon="home-o" to='/index'>首页</van-tabbar-item>
			  <van-tabbar-item icon="search" to='/allprodect'>全部菜品</van-tabbar-item>
			  <van-tabbar-item icon="wap-nav" to='/checklist'>常用清单</van-tabbar-item>
			  <van-tabbar-item icon="shopping-cart" to="/cart">购物车</van-tabbar-item>
			  <van-tabbar-item icon="manager" to='/me'>我的</van-tabbar-item>
		</van-tabbar>
		
		</div>
		
		
	</div>
</template>

<script>
	export default{
		name:"Footer",
		data(){
			return{
				active:'',
			}
		}
	}
</script>

<style scoped>
	#footer{
		display: flex;
		flex-direction:column;
		height: 100%;
	}
	#footer_body{
		flex: 1;
	}
	#footer_footer{
		height: 46px;
	}
</style>


