<template>
    <div style="margin-top:46px">
        <van-cell-group>
            <van-field
                v-model="username"
                required
                clearable
                label="用户名"
                right-icon="question-o"
                placeholder="请输入用户名"
                @click-right-icon="$toast('question')"
                @focus='test()'
            />
            <van-field
                v-model="password"
                type="password"
                label="密码"
                placeholder="请输入密码"
                :error-message="tishi"
                required
                @focus='test()'
            />
        </van-cell-group>
        <div style="display:flex; justify-content: space-around;">
            <van-button type="primary" icon="star-o" @click="login()">登陆</van-button>
            <van-button type="info" icon='smile-o' to="register">注册</van-button>
        </div>
  
    </div>
</template>

<script>
import axios from 'axios';
import { NoticeBar } from 'vant';

export default {
    name:'Login',
    data(){
        return{
            title:'登陆',
            username:'',
            password:'',
            tishi:'',
        }
    },
    mounted(){
        this.$emit('toparent',this.title)
    },
    methods: {
        //密码框获取焦点时，提示信息消失
        test(){
            this.tishi='';
        },
        //点击登陆
        login(){
            axios({
                method:'post',           
                url:'http://106.12.52.107:8081/MeledMall/user/login',
                params:{phonenum:this.username,password:this.password}

            }).then((data)=>{
                console.log(data.data);
                if(data.data.info=='登录失败,该账号不存在！'){
                    this.tishi='登录失败,该账号不存在！'
                }
                if(data.data.info=='账户或密码错误，请重新输入'){
                    this.tishi='账户或密码错误，请重新输入'
                }
                if(data.data.code==1){
                    this.$router.push('/index')
                    localStorage.setItem('username',this.username)
                    localStorage.setItem('token',data.data.info.id)
                }

            })
           
        }
    },

}
</script>

<style scoped>

</style>



