<template>
  <div class="hello">
  	<div class="head" >
  		<van-icon name="service-o" color="red" size='20' @click='tap()'/>
  		<div>
  			<van-search :fixed='true' placeholder="请输入搜索关键词" v-model="value1" @input='tap2()' />
  		</div>
  		<!-- <van-overlay
		:show="show"
		@click="show = false"
		/> -->

  		<van-icon name="chat-o" color='red' size='20' @click='tap1()'/>
  	</div>
  	<ul v-if="bian">
  		<router-link tag='li'  v-for="item in show1" :to="{name:'/detail',query:{id:item.id}}">{{item.name}}</router-link>
  		<!-- <li v-if="bian" v-for="item in show1">{{item.name}}</li> -->

  	</ul>
  	
  	<!--轮播图-->
  	<div style=";margin-bottom:20px;margin-top:20px">
  		
  
  	<van-swipe :autoplay="3000" indicator-color="white" style='height: 200px;'>
		  <van-swipe-item  v-for='item in data'><img :src="item.picture" style="width:100%;height: 200px;"/></van-swipe-item>
		  
		</van-swipe>
			</div>
		
  	<van-grid :border="false" :column-num="5">
  <van-grid-item v-for='item in list'>
    <van-image :src="item.photo" />
    <h6>{{item.menu}}</h6>
  </van-grid-item>
  
</van-grid>
			<hr />
			
			<div class="miaosha">
				<h4>超值秒杀</h4>
				<div class="time">
					<span>距离19点03场结束还剩:</span>
					<van-count-down :time="time">
						  <template v-slot="timeData">
						    <span class="item">{{ timeData.hours }}</span>
						    <span class="item">{{ timeData.minutes }}</span>
						    <span class="item">{{ timeData.seconds }}</span>
						  </template>
					</van-count-down>
			
				</div>
			</div>
			<van-grid :gutter="10" :column-num="3">
			  <van-grid-item
			    v-for="value in 3"
			    :key="value"
			    icon="photo-o"
			    text="文字"
			  />
			</van-grid>
			<hr />
			
			<div class="tehui">
				<div class="l-te">
					生鲜直降
				</div>
				<div class="r-te">
					<span>蔬菜特惠</span>
					<span>粮油专区</span>
					
					
				</div>
				
			</div>
			<van-tabs v-model="active" style='margin-bottom: 50px;'>
			  <van-tab title='为我推荐'>
			  	
			  	<div style="width: 90%;margin: 0 auto;">
			  		
			  		<van-card v-for='item in arr'
						  num="2"
						  :price="item.price"
						 :desc="item.desc"  
						  :title="item.name"
						  :thumb="item.photo"
						>
						<div slot="footer">
						    
						    <van-icon name="add" size="40px"/>
						</div>
						</van-card>
			  		
			  	</div>
			  	
			  	
			  	
			  </van-tab>
			  <van-tab title="我的清单">内容 2</van-tab>
			  
			</van-tabs>

			
   </div>
   
</template>

<script>
	import axios from 'axios';
	import qs from 'Qs';
export default {
  name: 'Index',
  data(){
  	return{
  		value1:'',
  		time:30*60*60*1000,
  		active: 0,
  		data:[],
  		list:[],
  		arr:[],
  		suo:[],
  		bian:false,
  		show: false
  	}
  },
  computed:{
  
  	show1(){
  		let arr1=[];
  		this.suo.map((item)=>{
  				if(item.name.search(this.value1)!=-1){
  					arr1.push(item)
  				}
  				
  			})
  		return arr1
  		
  	}
  },
  methods:{
  	tap(){
  		this.$router.push('/server')
  	},
  	tap1(){
  		this.$router.push('/message')
  	},
  	tap2(){

		//   this.show=true
  		this.bian=!this.bian
  		if(this.value1==''){
  			this.bian=false;
		  }
		  console.log(1)
  	}
  	
  	
  },
  mounted(){
  	axios({
  		method:'post',
  		url:'http://106.12.52.107:8081/MeledMall/sildeShow/show',
  	}).then((data)=>{
  		//console.log(data.data)
  		this.data=data.data.info
  		//console.log(this.data)
  	}),
  	//10个商品：
  	axios({
  		method:'post',
  		url:'http://106.12.52.107:8081/MeledMall/menu/parentMenu'
  	}).then((data)=>{
  		//console.log(data.data);
  		this.list = data.data.info;
  		//console.log(this.list)
  	}),
  	//为我推荐接口：
  	axios({
  		method:'post',
  		url:'http://106.12.52.107:8081/MeledMall/menu/recommend'
  	}).then((data)=>{
  		//console.log(data)
  		this.arr = data.data.info;
  		//console.log(this.arr);
  	}),
  	//搜索功能：
  	
  		
  		axios({
  			method:'post',
  			url:'http://106.12.52.107:8081/MeledMall/menu/selectChild',
  			
  		}).then((data)=>{
  			//console.log(data)
  			
  			this.suo=data.data.info;
  			console.log(this.suo)
  			
  			
  		})
  }
  
 }
</script>
	
	
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
	margin: 0;
	padding: 0;
}
	.head{
		
		background: blue;
		width: 100%;
		height: 40px;
		display: flex;
		justify-content: space-around;
		align-items: center;
		
	}
	.item {
  display: inline-block;
  width: 22px;

  color: #fff;
  font-size: 12px;
  text-align: center;
  background-color: #1989fa;
}
.miaosha{
	width: 100%;
	height: 50px;
	background: red;
	display: flex;
	justify-content: space-between;
	align-items: center;
	
}
.time{
	display: flex;
}
.tehui{
	width: 100%;
	height: 200px;
	background: red;
	display: flex;
	justify-content: space-around;
	align-items: center;
}
.l-te,.r-te{
	width: 45%;
	height: 180px;
	background: greenyellow;
	text-align: center;
}
.r-te {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
}
.r-te span{
	display: block;
	width: 150px;
	height: 80px;
	background: #1989FA;
	
}

</style>



