<template>
    <div style="margin-top:46px">
            <van-cell-group>
                <van-field
                    type="username"
                    v-model="phone"
                    label="手机号"
                    placeholder="请输入手机号"
                    :error-message="tishi"
                    required
                    @blur="test()"
                    
                />
            <van-field
                v-model="password"
                type="password"
                label="密码"
                placeholder="请输入6-26位密码"
                :error-message="tishi1"
                required
                @focus='test1()'
            />
        </van-cell-group>
        <div style="display:flex; justify-content: space-around;">
            <van-button type="primary" icon="star-o"  to="/login">登陆</van-button>
            <van-button :disabled="disabled" type="info" icon='smile-o' @click="register()">注册</van-button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:'Register',
    data(){
        return{
            title:'注册',
            phone:'',
            password:'',
            tishi:'',
            tishi1:'',
            disabled:false,            
            
        }
    },
    mounted(){
        this.$emit('toparent',this.title)
    },
    methods: {

        //验证手机号码：
        test(){
            var pReg=/^1[3-9][0-9]{9}$/;
            if(pReg.test(this.phone)==false){
                this.tishi='手机号格式错误';
                this.disabled=true;

            }else{
                this.tishi='';
                this.disabled=false;
            }

        },
        //密码框获取焦点时，提示信息消失
        test1(){
            this.tishi1='';
        },
        //注册调用接口
        register(){
            var pwdReg = /^[a-zA-Z0-9]{6,26}$/;
            if(pwdReg.test(this.password)==false){
                this.tishi1='密码格式错误，请重新输入';
                this.password='';
            }else{
                axios({
                    method:'post',
                    url:'http://106.12.52.107:8081/MeledMall/user/register',
                    params:{phonenum:this.phone,password:this.password}
                }).then((data)=>{
                    console.log(data.data);
                   // let data=data.data;
                     if(data.data.code==1){
                         this.$router.push('/login');
                     };
                     if(data.data.code==0){
                         this.tishi1='注册失败，请重新注册';
                     };
                     if(data.data.info=='该用户已存在！'){
                         this.tishi1='该用户已存在!请重新注册';
                     }
                })
            }


        }
    },
}
</script>

<style scoped>

</style>



