<template>
    <div style="margin-top:50px">
        <van-panel title="充值"  status="活动中">
            <div style="color:#ccc;font-size:14px">充值送优惠券</div>
        </van-panel>
        <van-cell-group>
            <van-field label="充值" v-model="value" placeholder="请输入充值金额" />
        </van-cell-group>
        <van-button type="primary" @click="chongzhi()">充值</van-button>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:'Chongzhi',
    data(){
        return{
            title:'充值中心',
            value:'',
        }
    },
    methods: {
        chongzhi(){
            var id = localStorage.getItem('token')
            console.log(this.value)
            console.log(id)
            this.$emit('toparent',this.title);
            axios({
                method:'post',
                url:'http://106.12.52.107:8081/MeledMall/user/recharge',
                params:{balance:this.value,id:id}
            }).then((data)=>{
                console.log(data.data)
                if(data.data.code==0){
                    this.$toast('充值成功')
                }
            })
        }
        

    }
}
</script>

<style scoped>

</style>



