<template>
	<div>
		<div class="head" >
  		<van-icon name="service-o" color="red" size='20' @click='tap()'/>
  		<div>
  			<van-search placeholder="请输入搜索关键词" v-model="value" @input='tap2()' />
  		</div>
  		
  		<van-icon name="bill" color='red' size='20' @click='tap1()'/>
  	</div>
  	<!--标签滚动-->
  	
  	<van-tabs @click="onClick" >
	  <van-tab v-for='item in data' :title="item.menu " :name="item.id">

	    <router-link tag="div" v-for="i in arr" :to='{name:"/detail",query:{id:i.id}}' style="margin-top:10px">		
		<van-card			
			:price="i.price"
			:desc="i.desc"  
			:title="i.name"
			:thumb="i.photo"
			/>		
		</router-link>
		<hr/>
	  </van-tab>
	</van-tabs>
	  	
  	
	</div>
</template>

<script>
	import axios from 'axios';
	
	export default{
		name:"Allprodect",
		data(){
			return{
				value:'',
				data:[],
				arr:[],

			}
		},
		methods: {
			onClick(name){
			    axios({
						
						method:'post',
						url:'http://106.12.52.107:8081/MeledMall/menu/childMenu',
						params:{id:name}
					}).then((data)=>{
						
						this.arr=data.data.info
						//console.log(this.arr)
					})
			},
			tap(){
				this.$router.push('/server')
			},
			 tap1(){
            this.$router.push('/youhuiquan')
        }

		},
		mounted(){
			axios({
					method:'post',
					url:"http://106.12.52.107:8081/MeledMall/menu/parentMenu"
				}).then((data)=>{
					this.data=data.data.info
				//	console.log(this.data)	
				})
			axios({
				
				method:'post',
				url:'http://106.12.52.107:8081/MeledMall/menu/childMenu',
				params:{id:1}
			}).then((data)=>{
				
				this.arr=data.data.info
				//console.log(this.arr)
					})
			
		}
	}
	
</script>


<style scoped>
	*{
	margin: 0;
	padding: 0;
}
	.head{
		
		background: blue;
		width: 100%;
		height: 40px;
		display: flex;
		justify-content: space-around;
		align-items: center;		
	}
	
</style>


