<template>
    <div id="tiJiao">
        <div class="ti_Body">
            <van-card v-for="(item,index) in list"
                :num="item.mnum"
                :price="item.mprice" 
                :title='item.mname'
                :thumb="item.mphoto"   
            >            
                <div slot="footer">
                    <van-checkbox  v-model="item.check" @click="gouxuan(item.check,index)" >勾选买买买</van-checkbox>
                    <van-stepper v-model="item.mnum"   @change='change(item.mnum,item.uid,item.mid,index)'/>
                    <van-button size="mini" type="warning" @click="del(item.mid)">删除</van-button>
                </div>
            </van-card>
        </div>
        <div class="ti_Bottom" >
            <van-submit-bar 
            :price="sum"
            button-text="提交订单"
            @submit="onSubmit"
            >
            <van-checkbox v-model="checked" @click="allCheck()">全选</van-checkbox>
        `   </van-submit-bar>
        </div>        
    </div>
</template>
<script>
import axios from 'axios';
export default {
    components:{
        'v-cart':{
            template:'#Tijiao'
        }
    },
    //name:"Tijiao",
    data(){
        return{
            checked:false,
            list:[],
            data:[],
            addValue:'',
            sum:0,  
            numa :0,   //记录勾选的数量，来判断全选按钮的状态
              
        }
    },    
    mounted(){
        var uid = localStorage.getItem('token');
            //展示购物车数据：
        axios({
            method:'post',
            url:'http://106.12.52.107:8081/MeledMall/shopCar/showShopCar',
            params:{uid:uid}
        }).then((data)=>{
            for(var i=0;i<data.data.info.length;i++){
                data.data.info[i].check=false;
              
            }
            this.list=data.data.info;
            console.log(this.list)
        })
    },
    methods:{
        //单个勾选时：
        gouxuan(a,index){//a的初始值为false；
            console.log(this.list)
            console.log(a,index)//false/1
           var count = parseFloat(this.list[index].mprice) *  parseFloat(this.list[index].mnum) * 100;
            console.log(count) //count为计算一件商品的总价
            // ;//勾选框选中的数量
            if(!a){//当勾选框选中时：
                this.sum += count;
                this.numa ++;
               // console.log(this.numa)
            }else{
                //取消时：
                this.sum -= count;
                this.numa --;
                // console.log(this.numa)
            }
            if(this.list.length == this.numa){
                this.checked = true;
            }else{
                this.checked=false;
            }

        },
        //全选按钮：
        allCheck(){
            if(!this.checked){//选中时：
                for(var i of this.list){
                    i.check = true;
                    this.sum  += i.mprice * i.mnum * 100;
                }
            }else{
               for(var i of this.list){
                    i.check = false;
                    this.sum = 0;
                }
            }
            
        },

       // tap(a,index){
            // if(!a.check){
            //     a.check=true;

            // }else{
            //     a.check=false;
            // }
            // console.log(a.check);
            
       // },
       // quanxuan(a){
            // if(!a){
            //     this.check=true;
            //     console.log(this.check);
            //     for(var i=0;i<this.list.length;i++){
            //         this.list[i].check=true;
            //     }
            // }else{
            //     this.check=false;
            //     console.log(this.check);
            //     for(var i=0;i<this.list.length;i++){
            //         this.list[i].check=false;
            //     }
            // }
       // },
        onClickLeft() {
           this.$router.go(-1);
        },
        onSubmit(){
            if(this.sum==0){
                this.$toast('请勾选需要的商品')
            }else{
                this.$router.push('/buy')
            }
            
        },
        //删除购物车：
        del(mid){
             var uid = localStorage.getItem('token');
            
            axios({
                method:'post',
                url:'http://106.12.52.107:8081/MeledMall/shopCar/deleteMenu',
                params:{uid:uid,mid:mid}
            }).then((data)=>{
                
                this.data = data.data;
                console.log(this.data)
                if(this.data.code==1){
                    axios({
                        method:'post',
                        url:'http://106.12.52.107:8081/MeledMall/shopCar/showShopCar',
                        params:{uid:uid}
                    }).then((data)=>{
                        
                        this.list=data.data.info;
                        //console.log(this.list)
                    })
                            }
                        })           
        },
        //加和减：  方法（一）
        // change(mnum,uid,mid,index){
        //     //掉购物车的数据接口，得到加，减改变之前的值，
        //      axios({
        //             method:'post',
        //             url:'http://106.12.52.107:8081/MeledMall/shopCar/showShopCar',
        //             params:{uid:uid}
        //     }).then((data)=>{
        //             var aa=data.data.info[index].mnum;
        //             console.log(aa,mnum);
        //             //调用修改购物车数据的接口，得到改变之后产品的数量，于改变之前的进行比较，来决定总价是加还是减
        //               axios({
        //                     method:'post',
        //                     url:'http://106.12.52.107:8081/MeledMall/shopCar/editMnum',
        //                     params:{mnum:mnum,uid:uid,mid:mid}
        //                 }).then((data)=>{
        //                     // console.log(data);
        //                     if(this.list[index].check){//勾选框选中时触发
        //                         //console.log('aaa')
        //                         if(aa>mnum){
        //                             this.sum -= this.list[index].mprice * 1 * 100;//减一次的值
        //                         }else{
        //                             this.sum += this.list[index].mprice * 1 * 100;//加一次的值
        //                         }
                                
        //                     //   this.sum+=this.list[index].mnum*this.list[index].mprice*1*100;
        //                     }                       
        //                 })               
        //         })
            
        // },

        // 加和减：  方法（二）
        change(mnum,uid,mid,index){
            
              axios({
                method:'post',
                url:'http://106.12.52.107:8081/MeledMall/shopCar/editMnum',
                params:{mnum:mnum,uid:uid,mid:mid}
            }).then((data)=>{
                this.add_Jian();
            })

        },
//重新计算所有产品的价格的总和，付给sum;
        add_Jian(){
            this.sum = 0*0;
            this.list.forEach((item,index) => {
                    if(item.check){//判断商品是否选中
                    console.log(item.check)//选中为false
                        this.sum += item.mprice*item.mnum*100 //金额计算
                    }
                });
        }   
    },
}
</script>

<style scoped>
     #tiJiao{
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        
    }
    .ti_Body{
        flex: 1;
        /* background: red; */
        overflow: auto;

    }
    .ti_Bottom{
        width: 100%;
        height: 50px;
        /* background: pink; */
    } 
</style>



