import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import Supporter from '@/views/Supporter'
import Server from '@/components/Server'
import Message from '@/components/Message'
import Footer from '@/views/Footer'
import Allprodect from '@/views/Allprodect'
import Detail from '@/views/Detail'
import Checklist from '@/views/Checklist'
import Cart from '@/views/Cart'
import Tijiao from '@/components/Tijiao'
import Buy from '@/components/Buy'
import Me from '@/views/Me'
import Setting from '@/components/Setting'
import Chongzhi from '@/components/Chongzhi'
import Youhuiquan from '@/components/Youhuiquan'

import Login from '@/components/Login'
import Register from '@/components/Register';
Vue.use(Router)

export default new Router({
  routes: [
  	{
  		path:'/',
  		redirect:'/index',
  		component:Index
  	},
  	{
  		path:'/footer',
  		redirect:'/index',
  		component:Footer,
  		children:[
  			{
			path: '/index',
			name: 'Index',
			component: Index
			},
			{
			path:'/allprodect',
			component:Allprodect
			},
			{
			path:'/checklist',
			component:Checklist
			},
			{
			path:'/cart',
			redirect:'/tijiao',
			component:Cart,
			children:[
				{
					path:'/tijiao',
					component:Tijiao,
				},
				{
					path:'/buy',
					component:Buy,
				}
			]

			},
			{
				path:'/me',
				component:Me,
			}
  			
  		]
	  },
	  

    {
    	path:'/supporter',
    	component:Supporter,
    	children:[
			//服务：
    	{
    		path:'/server',
    		component:Server,
		},
		//消息：
    	{
    		path:'/message',
    		component:Message,
		},
		//详情：
		{
			path:'/detail',
			name:'/detail',
			component:Detail,
		},
		//设置：
		{
			path:'/setting',
			component:Setting,
		},
		//登陆：
		{
			path:'/login',
			component:Login
		},
		//注册：
    	{
			path:'/register',
			component:Register
		},
		//充值
		{
			path:'/chongzhi',
			component:Chongzhi
		},
		//优惠券：
		{
			path:'/youhuiquan',
			component:Youhuiquan
		}
    	]
	},
	
    
  ]
})



