<template>
    <div>
        <h2>dasfa</h2>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:'Youhuiquan',
    data(){
        return{
            title:"优惠券",
        }
    },
    mounted(){
         var id = localStorage.getItem('token');
        this.$emit('toparent',this.title);
        axios({
            method:'post',
            url:'http://106.12.52.107:8081/MeledMall/coupon/showCoupon',
            params:{id:id}
        }).then((data)=>{
            console.log(data)
        })
    }
}
</script>

