<template>
	<div id='Mess'>
		<div id="Mess_top">

		</div>
		<div id="Mess_body">
            <van-card v-for="item in list"
				:desc="item.messages"  
				:title="item.type"
				:thumb="item.picture"
				/>
		</div>
		<div id="Mess_bottom">

		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default{
		name:'Message',
		data(){
			return{
				title:'消息中心',
				list:[],
			}
			
		},
		methods:{
			
		},
		mounted(){
			this.$emit('toparent',this.title);
			axios({
				method:'post',
				url:'http://106.12.52.107:8081/MeledMall/msg/findType'
			}).then((data)=>{
				//console.log(data.data.info)
				this.list = data.data.info;
			})
		}
	}
</script>

<style scoped>
#Mess{
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
}
#Mess_top,#Mess_bottom{
	width: 100%;
	/* background: pink; */
	height:46px;
}
#Mess_body{
	flex: 1;
	/* background: red; */
	overflow: auto
}
</style>


